<template>
  <div class="page chat">
    <topheader class="topheader"
               :u="u" :chat="chat" @addMember="addMemberModal"
    />

    <maincontent v-if="!roomInfo">
      <template v-slot:content>
        <chat @sending="sending"
              @newchat="newchat"
              :u="u" :chat="chat"
              @removeBrokenRoom="creatorLeft"
              @getEvents="eventsRoom"/>
      </template>
    </maincontent>

    <modal @close="closeModal" v-if="openInviteModal">
      <template v-slot:header>
        <span>{{ $t("caption.inviteUser") }}</span>
      </template>
      <template v-slot:body>
        <contacts :mode="`inviteUsers`" :chatRoomId="chat.roomId" @closeModal="closeContactModal"/>
      </template>
      <template v-slot:footer></template>
    </modal>
  </div>
</template>

<style scoped lang="sass">

.topheader
  width: 100%
  top: 0
  z-index: 999

.aboutContact
  position: absolute
  left: 0
  right: 0
  top: 100px
  bottom: 0
  z-index: 999
  background: $color-applebuttontext

/deep/ #maincontent .headerSpacerWrapper
  bottom: 0
</style>

<script>

import chat from '@/components/chat/index.vue';
import topheader from '@/components/chat/topheader/index.vue';
import contacts from '@/components/contacts/index.vue';
import {mapState} from 'vuex';
import * as _ from "underscore";

export default {
  name: 'pagechat',
  components: {
    chat,
    topheader,
    contacts
  },
  data: () => {
    return {
      roomInfo: false,
      m_chat: {},
      events: [],
      openInviteModal: false,
      brokenRoom: false,

    }
  },

  computed: {
    u() {
      return this.$route.query.u
    },
    chat() {
      var id = this.$route.query.id
      return this.$store.state.chatsMap[id]
    },

    ...mapState({
      pocketnet: state => state.pocketnet,
      minimized: state => state.minimized,
    }),

  },
  beforeRouteLeave: function (to, from, next) {
    if (this.brokenRoom) {
      this.core.mtrx.client.leave(from.query.id).then(r => {
        this.core.mtrx.client.forget(from.query.id, true).then(r => {
          return r
        }).then(r => {
          this.$store.commit('DELETE_ROOM', from.query.id);
          console.log(this.$store.state.chats, "removed chat")
        }).then(r => {
          next();
        })
      })
    } else {
      next()

    }
  },
  mounted() {

  },
  methods: {
    creatorLeft(val) {
      console.log("in chat page", val)
      this.brokenRoom = val
    },
    closeModal() {
      this.openInviteModal = false
    },

    closeContactModal(value) {
      this.openInviteModal = value
    },
    addMemberModal(value) {
      this.openInviteModal = value
    },
    newchat(_chat) {

    },
    sending() {
    },
    eventsRoom(data) {
      this.events = data
    },

  },


}
</script>
